import React, { useState } from "react";
import { Drawer, List, ListItem, ListItemButton, ListItemIcon, ListItemText, IconButton, Divider, Box } from "@mui/material";
import { Home, Dashboard, Menu } from "@mui/icons-material";
import StatusAlert from "./StatusAlert"; // ✅ Import StatusAlert

const Sidebar = ({ toggleDarkMode, darkMode }) => {
  const [open, setOpen] = useState(false);

  const toggleDrawer = () => setOpen(!open);

  return (
    <>
      {/* ✅ Sidebar Toggle Button with Custom Color */}
      <IconButton
        onClick={toggleDrawer}
        sx={{ 
          position: "absolute", 
          top: 16, 
          left: 16, 
          color: "white", 
          bgcolor: "primary.main", // ✅ Change button color
          "&:hover": { bgcolor: "primary.dark" } // ✅ Darker on hover
        }}
      >
        <Menu />
      </IconButton>

      <Drawer
        anchor="left"
        open={open}
        onClose={toggleDrawer}
        PaperProps={{ sx: { width: 270, bgcolor: "background.default", color: "text.primary" } }}
      >
        {/* ✅ Status Alert Section */}
        <Box sx={{ p: 3, textAlign: "center", bgcolor: "primary.main", color: "white" }}>
          <StatusAlert pm25={50} />
        </Box>

        <Divider />

        {/* ✅ Sidebar Navigation */}
        <List>
          <ListItem disablePadding>
            <ListItemButton href="/">
              <ListItemIcon><Home sx={{ color: "primary.main" }} /></ListItemIcon>
              <ListItemText primary="Home" />
            </ListItemButton>
          </ListItem>
          <ListItem disablePadding>
            <ListItemButton href="/dashboard">
              <ListItemIcon><Dashboard sx={{ color: "primary.main" }} /></ListItemIcon>
              <ListItemText primary="Dashboard" />
            </ListItemButton>
          </ListItem>
        </List>
      </Drawer>
    </>
  );
};

export default Sidebar;
