import React, { useState, useEffect } from "react";
import { Chart, registerables } from "chart.js";
import { Line } from "react-chartjs-2";
import axios from "axios";
import { Container, Grid, Card, Typography, CircularProgress, Button, ButtonGroup, Paper, Box } from "@mui/material";
import { motion, AnimatePresence } from "framer-motion";
import LiveDataCards from "../components/LiveDataCards";
import LiveDataTable from "../components/LiveDataTable";
import StatusAlert from "../components/StatusAlert";

Chart.register(...registerables);

const API_URL = "http://localhost:8000/data";
const WS_URL = "ws://localhost:8000";

const MAX_POINTS = 20;

const Dashboard = () => {
  const [data, setData] = useState([]);
  const [pm25Chart, setPm25Chart] = useState(null);
  const [tempChart, setTempChart] = useState(null);
  const [humidityChart, setHumidityChart] = useState(null);
  const [windChart, setWindChart] = useState(null);
  const [activeChart, setActiveChart] = useState("pm25");

  useEffect(() => {
    axios.get(API_URL).then((response) => {
      const initialData = response.data.slice(-MAX_POINTS);
      setData(initialData);
      updateCharts(initialData);
    });

    const socket = new WebSocket(WS_URL);
    socket.onopen = () => console.log("✅ WebSocket Connected");

    socket.onmessage = (event) => {
      const newData = JSON.parse(event.data);
      setData((prevData) => {
        const updatedData = [...prevData, newData].slice(-MAX_POINTS);
        updateCharts(updatedData);
        return updatedData;
      });
    };

    socket.onerror = (error) => console.error("🚨 WebSocket Error:", error);
    socket.onclose = () => console.warn("⚠️ WebSocket Disconnected");

    return () => socket.close();
  }, []);

  const updateCharts = (data) => {
    const timestamps = data.map((d) => d.time);
    setPm25Chart(createChartData(timestamps, data.map((d) => d.pm25), "PM2.5 Levels", "red"));
    setTempChart(createChartData(timestamps, data.map((d) => d.temp), "Temperature (°C)", "blue"));
    setHumidityChart(createChartData(timestamps, data.map((d) => d.humidity), "Humidity (%)", "green"));
    setWindChart(createChartData(timestamps, data.map((d) => d.wind), "Wind Speed (km/h)", "purple"));
  };

  const createChartData = (labels, data, label, color) => ({
    labels,
    datasets: [{ label, data, borderColor: color, fill: false }],
  });

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <Typography variant="h3" color="primary" gutterBottom textAlign="center">
        Real-Time Air Quality Dashboard
      </Typography>

      <Typography variant="body1" textAlign="center" color="textSecondary" sx={{ mb: 3 }}>
        Monitor live air quality metrics with real-time updates.
      </Typography>

      {/* ✅ Status Alert Section */}
      <Paper
        elevation={3}
        sx={{
          p: 3,
          mb: 4,
          borderRadius: 2,
          bgcolor: data.length > 0 && data[data.length - 1].pm25 > 100 ? "error.main" :
                   data.length > 0 && data[data.length - 1].pm25 > 50 ? "warning.main" : "success.main",
          color: "white",
          textAlign: "center",
        }}
      >
        {/* <Typography variant="h5" fontWeight="bold">Air Quality Alert</Typography> */}
        <StatusAlert pm25={data.length > 0 ? data[data.length - 1].pm25 : 0} />
      </Paper>

      <LiveDataCards data={data.length > 0 ? data[data.length - 1] : {}} />

      {/* ✅ Chart Toggle Buttons */}
      <ButtonGroup fullWidth variant="contained" sx={{ my: 3 }}>
        <Button onClick={() => setActiveChart("pm25")} color={activeChart === "pm25" ? "primary" : "inherit"}>PM2.5</Button>
        <Button onClick={() => setActiveChart("temp")} color={activeChart === "temp" ? "primary" : "inherit"}>Temperature</Button>
        <Button onClick={() => setActiveChart("humidity")} color={activeChart === "humidity" ? "primary" : "inherit"}>Humidity</Button>
        <Button onClick={() => setActiveChart("wind")} color={activeChart === "wind" ? "primary" : "inherit"}>Wind Speed</Button>
      </ButtonGroup>

      <Grid container justifyContent="center">
        <Grid item xs={12} md={8}>
          <Card sx={{ p: 4, boxShadow: 3, textAlign: "center", overflow: "hidden" }}>
            <AnimatePresence mode="wait">
              {activeChart === "pm25" && (
                <motion.div
                  key="pm25"
                  initial={{ opacity: 0, x: -50 }}
                  animate={{ opacity: 1, x: 0 }}
                  exit={{ opacity: 0, x: 50 }}
                  transition={{ duration: 0.5 }}
                >
                  <Typography variant="h6" color="secondary">PM2.5 Levels</Typography>
                  {pm25Chart ? <Line data={pm25Chart} /> : <CircularProgress />}
                </motion.div>
              )}
              {activeChart === "temp" && (
                <motion.div
                  key="temp"
                  initial={{ opacity: 0, x: -50 }}
                  animate={{ opacity: 1, x: 0 }}
                  exit={{ opacity: 0, x: 50 }}
                  transition={{ duration: 0.5 }}
                >
                  <Typography variant="h6" color="secondary">Temperature (°C)</Typography>
                  {tempChart ? <Line data={tempChart} /> : <CircularProgress />}
                </motion.div>
              )}
              {activeChart === "humidity" && (
                <motion.div
                  key="humidity"
                  initial={{ opacity: 0, x: -50 }}
                  animate={{ opacity: 1, x: 0 }}
                  exit={{ opacity: 0, x: 50 }}
                  transition={{ duration: 0.5 }}
                >
                  <Typography variant="h6" color="secondary">Humidity (%)</Typography>
                  {humidityChart ? <Line data={humidityChart} /> : <CircularProgress />}
                </motion.div>
              )}
              {activeChart === "wind" && (
                <motion.div
                  key="wind"
                  initial={{ opacity: 0, x: -50 }}
                  animate={{ opacity: 1, x: 0 }}
                  exit={{ opacity: 0, x: 50 }}
                  transition={{ duration: 0.5 }}
                >
                  <Typography variant="h6" color="secondary">Wind Speed (km/h)</Typography>
                  {windChart ? <Line data={windChart} /> : <CircularProgress />}
                </motion.div>
              )}
            </AnimatePresence>
          </Card>
        </Grid>
      </Grid>
    </Container>
  );
};

export default Dashboard;
